.container {
    width: 400px;
}
.flex {
    display: flex;
}
.flex.flex-wrap {
    flex-wrap: wrap;
    gap: 5px;
    margin-top: 10px;
}
.letter {
    display: block;
    width: 34px;
    height: 34px;
    border: 1px solid gray;
    line-height: 37px;
    margin-left: -1px;
    text-align: center;
}
.button {
    width: 34px;
    height: 34px;
    line-height: 0;
}
.guessed,
.hidden {
    display: none;
}
